let getType = ['$', '',
    'VARIABLE', 'STRING',
    'FLOAT', 'INTEGER','NOT',
    'EQUAL', 'SIGN', 'SIGN', 'MATH_OPERATOR', 'MATH_OPERATOR', 'MATH_OPERATOR', 'POWER',
    'DELIMITER',
    'LPAREN', 'RPAREN',
    'OPEN_STATEMENT',
    'CLOSE_STATEMENT',
    'AS', 'IFN',
    'LOOP', 'ON',
    'BY', 'SAY','GET','FINALLY',
    'UNTIL', 'COMP1','COMP2','AND','OR','COMMENT'];



    const isTerminal = (token) =>{
      return getType2[token];
    }






    
const getType2 = {
'VARIABLE':true, 'STRING':true,
'FLOAT':true, 'INTEGER':true,'NOT':true,
'EQUAL':true, 'SIGN':true, 'SIGN':true, 'MATH_OPERATOR':true, 'MATH_OPERATOR':true, 'MATH_OPERATOR':true, 'POWER':true,
'DELIMITER':true,
'LPAREN':true, 'RPAREN':true,
'OPEN_STATEMENT':true,
'CLOSE_STATEMENT':true,
'AS':true, 'IFN':true,
'LOOP':true, 'ON':true,
'BY':true, 'SAY':true,'GET':true,'FINALLY':true,
'UNTIL':true, 'COMP1':true,'COMP2':true,'AND':true,'OR':true,'COMMENT':true,'$':true};





// Stack = Function
// Tokens [$,S]

const recToGod = (rule, tokens, data2)=>{
    if(!tokens?.length)return [];
    if(isTerminal(rule)){
      let c = tokens.shift();
      console.log(c)
      return rule===getType[c.group] ?  c: undefined;
    }
    
    let rules = parseTable22[rule][getType[tokens[0].group]];
    let data = [];
    if(rules){
      if(rules[0]==='Lambda') return [];
      for(let i = 0; i < rules.length; i++){
        let result = recToGod(rules[i], tokens, data2);
        if(!result) return undefined;
        if(result.length !==0) data2.push(result);
        
      }
    }else return undefined;
     
     return data2;
}




const parser = (currentRule, tokens)=>{
  if(!tokens?.length)return [];
  if(isTerminal(currentRule)){
    let currentToken = tokens.shift();
    if(currentRule===currentToken) return currentToken;
    else return undefined;
  }
  let nextRule = parseTable[currentRule]?.[getType[tokens[0].group]];
  let data = [];
  if(nextRule){
    if(nextRule[0]==='epsilon') return [];
    for(let i = 0; i < nextRule.length; i++){
      let result = parser(nextRule[i], tokens);
      if(!result) return undefined;
      if(result.length !==0) data.push(result);
      
    }
  }else return undefined;
   return data.length > 1 ? data : data[0];
}